.home__content{
    grid-template-columns: 160px 440px repeat(2,1fr);
    padding-top: 5.5rem;
    column-gap: 2rem;
    align-items: center;
}
.home__container{

    row-gap: 7rem;
}
.home__img{
    background: url(../../assets/mypic.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    box-shadow: inset  0 0 0 9px rgb(255, 255, 255/40%);
    width: 300px;
    height: 300px;
    order: 1;
    justify-self: center;
    animation: profile_animation 8s ease-in-out infinite 0s;
}

@keyframes profile_animation {
    0%{
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
    50%{
        border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
    }
    100%{
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
    
}

.home__social{
    display: grid;
    grid-template-columns: max-content;
    row-gap: 1rem;
    
}

.home__social-icon{
    font-size: 2rem;
    color: var(--title-color);
}

.home__social-icon:hover{
    
    color: var(--title-color-dark);
}


.home__title{
    font-size: 3rem;
    margin-bottom: var(--mb-0-25);
}

.home__hand{
    width: 38px;
    height: 38px;
    margin-left: .5rem;
}

.home__subtitle{
    position: relative;
    font-size: var(--h3-font-size);
    padding-left: 5.4rem;
    margin-bottom: var(--mb-1);
    z-index: -10;
   
}

.home__subtitle::before{

    content: '';
    position: absolute;
    width: 70px;
    height: 1.2px;
    background-color: var(--text-color);
    left: 0;
    top: 1rem;
}

.home__description{
    max-width: 400px;
    margin-bottom: var(--mb-3);
}

.home__scroll{
    margin-inline: auto;
}

.wheel{
    animation: scroll 2s ease infinite;
}

@keyframes scroll {
    0%{
        transform: translateY(0);

    }

    100%{
        transform: translateY(25%);
    }
}
.home__scroll-button{
    display: flex;
    flex-direction: column;
    
}
.arrow-down-ani{
    
    animation: arrow-down 2s ease-in-out infinite;
}

@keyframes arrow-down {
    50%{
        transform: translateY(1.2rem);
    }
}
.home__scroll-name{
    color: var(--title-color);
    font-weight: var(--font-medium);
    margin-top: .5rem;
}

.home__scroll-arrow{
    margin-top: -.6rem;
    font-size: 1.8rem;
    color: var(--title-color);
}







@media screen and (max-width: 1080px) {
    
    .home__container{
        justify-content: center;
    }
    .home__content{

        grid-template-columns: 100px 320px repeat(1,1fr);
       
    }
    
    .home__hand{
        width: 32px;
        height: 32px;
    }

    .home__subtitle{
        
        font-size: var(--h3--font-size);
        margin-bottom: var(--mb-2);
    }

    .home__subtitle::before{
        padding-left: 3rem;
        top: .7rem;
        width: 20px;
        height: 1px;
    }


    .home__description{
        max-width: initial;
        margin-bottom: var(--mb-2-5);
    }

    .home__img{
        width: 270px;
        height: 270px;
        box-shadow: inset  0 0 0 7px rgb(255, 255, 255/40%);
    }

    .home__title{
        font-size: 2.2rem;
    }

    .home__social-icon{
        font-size: 2rem;
    }
}

@media screen and (max-width: 768px) {
    
    
    .home__content{
        grid-template-columns: 1.5rem 3fr;
        padding-top: 3.5rem;
    }

    .home__img{
        order: initial;
    }

    .home__data{
        
        grid-column: 1/3;
        
        text-align: center;
    }

    .home__subtitle{
        padding-left: 0;
        font-size: var(--h1-font-size);
    }

    .home__description{

        font-size: var(--h2-font-size);
    }

    .home__subtitle::before{
        display: none;
        
    }

    .home__scroll{
        display: none;
    }

    .home__social-icon{
        font-size: 2rem;
    }

    .button{
        padding: 1rem 1.75rem;
        font-size: 1rem;
    }

    .button__icon{
        width: 22px;
        height: 22px;
    }
}

@media screen and (max-width: 376px){
    :root{
        --big-font-size: 1.75rem;
        --h1-font-size: 1rem;
        --h2-font-size: .8rem;
        --h3-font-size: .6rem;
        --normal-font-size: 0.5rem;
        --small-font-size: 0.4rem;
        --smaller-font-size: 0.3rem;
      }
    .home__img{
        width: 200px;
        height: 200px;

    }
    .home__social-icon{
        font-size: 1.4rem;
    }
    .home__hand{
        width: 20px;
        height: 20px;
    }

    .home__title{

        font-size: 1rem;

    }
    .button{
        padding: 1rem 1.75rem;
        font-size: .75rem;
    }

    
}