.services__container{

    grid-template-columns: repeat(2,1fr);
    justify-content: center;
    column-gap: 1.5rem;

}
.services__content{

    border: 1px solid rgba(0,0,0,.1);
    background-color: var(--container-color);
    padding: 6rem 2rem;
    border-radius: 1rem;
}
.services__modal{
    display: none;

}

.services__icon{
    display: block;
    font-size: 2rem;
    color: var(--title-color);
    margin-bottom: var(--mb-1);
}

.services__title{
    font-size: var(--h2-font-size);
    margin-bottom: var(--mb-1);
    font-weight: var(--font-medium);
}

.services__button{
    color: var(--title-color);
    font-size: var(--h3-font-size);
    display: flex;
    align-items: center;
    column-gap: .25rem;
    cursor: pointer;

}

.services__button-icon{
    font-size: 1.3rem;
    transition: 0.3s;
}

.services__button:hover .services__button-icon{

    transform: translateX(10%);
}

.services__modal{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,.5);
    z-index: var(--z-modal);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem 2rem;
    opacity: 0;
    visibility: hidden;
    transition: .3s;
}

.services__modal-content{

    width: 550px;
    height: 25rem;
    position: relative;
    background-color: var(--container-color);
    padding: 2rem 2rem;
    border-radius: 1rem;

}

.services__modal-close{
    position: absolute;
    top: 2%;
    left: 90%;
    color: var(--title-color);
    font-size: 2rem;
    cursor: pointer;
}

.services__modal-title,
.services__modal-description{
    text-align: center;
}

.services__modal-title{
    font-size: var(--h2-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-1);
}

.services__modal-description{
    font-size: var(--small-font-size);
    padding: 1rem 1rem;
    margin-bottom: var(--mb-2);
}

.services__modal-services{
    row-gap: 1rem;
   
}

.services__modal-service{
    
    display: flex;
   column-gap: 1rem;
}


.services__modal-icon{
    font-size: 1.2rem;
}
.services__modal-info{
    font-size: var(--h3-font-size);
}

.active__modal{
    opacity: 1;
    visibility: visible;
}


@media screen and (max-width: 1080px) {
    .services__modal-close{
        position: absolute;
        top: .1%;
        left: 90%;
        color: var(--title-color);
        font-size: 2rem;
        cursor: pointer;
    }
    
    .services__modal-content{

        width: 400px;
        position: relative;
        background-color: var(--container-color);
        padding: 2rem 2rem;
        border-radius: 1rem;
    
    }
    
  }

  @media screen and (max-width: 768px) {
    
    .services__container{
        display: flex;
    }
    .services__content{
        width: 300px;
        
    }
    .services__title{
        font-size: 1.5rem;
    }
    .services__button{
        font-size: 1rem;
    }
    .services__modal-close{
        position: absolute;
        top: .1%;
        left: 87%;
        color: var(--title-color);
        font-size: 2rem;
        cursor: pointer;
    }
    .services__modal-content{

        width: 350px;
        height: 25rem;
        position: relative;
        background-color: var(--container-color);
        padding: 2rem 2rem;
        border-radius: 1rem;
    
    }
        
   
    .services__modal-info{
        font-size: var(--h2-font-size);
    }

    .services__modal-title{
        font-size: 1.5rem;
        font-weight: var(--font-medium);
        margin-bottom: var(--mb-0-5);
    }
    
    .services__modal-description{
        font-size: .9rem;
        padding: 1rem 1rem;
        margin-bottom: var(--mb-0-5);
    }

    .services__modal-info{
        font-size: 1rem;
    }

  }

  @media screen and (max-width: 568px) {
  

.services__container{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .services__content{
        width: 300px;
        
    }
    .services__title{
        font-size: 1.5rem;
    }
    .services__button{
        font-size: 1rem;
    }
    .services__modal-close{
        position: absolute;
        top: .1%;
        left: 87%;
        color: var(--title-color);
        font-size: 2rem;
        cursor: pointer;
    }
    .services__modal-content{

        width: 350px;
        height: 25rem;
        position: relative;
        background-color: var(--container-color);
        padding: 2rem 2rem;
        border-radius: 1rem;
    
    }
        
   
    .services__modal-info{
        font-size: var(--h2-font-size);
    }

    .services__modal-title{
        font-size: 1.5rem;
        font-weight: var(--font-medium);
        margin-bottom: var(--mb-0-5);
    }
    
    .services__modal-description{
        font-size: .8rem;
        padding: 1rem 1rem;
        margin-bottom: var(--mb-0-5);
    }

    .services__modal-info{
        font-size: 1rem;
    }

}