.footer{
    background-color: var(--container-color);
    border-top: 1px solid rgba(0,0,0,.1);
}

.footer__container{
    padding: 2rem 2rem;
}

.footer__title,.footer__link{
    color: var(--title-color);
}

.footer__title{
    text-align: center;
    margin-bottom: var(--mb-2);
}

.footer__link:hover{

    color: var(--title-color-dark);
}

.footer__list{
    display: flex;
    justify-content: center;
    column-gap: 1.5rem;
    margin-bottom: var(--mb-2);
}

.footer__social{
    
    display: flex;
    justify-content: center;
    column-gap: 1.5rem;
    margin-bottom: var(--mb-2);
    align-items: center;
   
}

.footer__social-link{
    font-size: 1.8rem;
    color: var(--title-color);
   
}

.footer__social-link:hover{
    color: var(--title-color-dark);
}

.footer__copyright{
    display: block;
    text-align: center;
    font-size: var(--small-font-size);
}

.footer__social-link:first-child{
    font-size: 1.8rem;
    padding-bottom: .2rem;
}

@media screen and (max-width: 768px) {
    
    .footer__title{

        font-size: 1.4rem;
    }
    .footer__link{
        font-size: var(--h2-font-size);
    }

    .footer__copyright{
        font-size: .6rem;
    }
  }

  
