.scrollup{
    width: 3rem;
    height: 3rem;
    position: fixed;
    right: 2.5rem;
    bottom: 3rem;
    background-color: var(--title-color);
    opacity: .8;
    padding: .5rem .5rem;
    border-radius: 50%;
    z-index: var(--z-tooltip);
    transition: .5s;
    display: flex;
    justify-content: center;
    align-items: center;
    visibility: hidden;
}

.scrollup__icon{
    font-size: 1.5rem;
    color: var(--container-color);
}

.scrollup:hover{
    background-color: var(--title-color-dark);
}