.navbar{
    width: 100%;
    position: fixed;
    top:0;
    left: 0;
    z-index: var(--z--fixed);
    background-color: var(--body-color);
}

.nav{
    height: calc(var(--header-heights) + 1.5rem);
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 1rem;
}


.nav__logo,.nav__toggle{
    
    color: var(--title-color-dark);
    font-weight: var(--font-medium);
    font-size: 1.2rem;
    display: flex;
    column-gap: 2rem;
}



.nav__list{

    display: flex;
    column-gap: 2rem;
    
}


.nav__link
{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 1.2rem;
    color: var(--title-color);
    font-weight: var(--font-medium);
    transition: all .2s linear;

}


.active-link,.nav__link:hover{

        color: var(--title-color-dark);

}




.nav__icon,.nav__close,.nav__toggle{
    display: none;
}



.navbar-shadow{
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.158);
}





@media screen and (max-width: 768px) {

    
    .nav__icon,
    .nav__close,
    .nav__toggle{
        
        display: block;
    }
    
    .nav__menu{
        
        position: fixed;
        top: -100%;
        left: 0;
        width: 100%;
        background-color: var(--body-color);
        padding: 2rem 1.5rem 4rem;
        box-shadow: 0 -1px 5px rgba(0, 0, 0, 0.25);
        border-radius: 0 0 1.5rem 1.5rem;
        transition: .2s;
    }

    .show-menu{
        top: 0;
    }

    .nav__toggle{

        font-size: 1.4rem;
        cursor: pointer;
    }

    

    .nav__icon{
        font-size: 1.75rem;
    }

    .nav__close{
        position: absolute;
        right: 1.3rem;
        top: 80%;
        font-size: 1.5rem;
        
        cursor: pointer;
        color: var(--title-color);
    }

    .nav__close:hover{

        color: var(--title-color-dark);
    }
    
    .nav__list{
        display: grid;
        grid-template-columns: repeat(3,1fr);
        gap: 2rem;
    }
    
    .nav__link{
        font-size: var(--h3-font-size);
    }

  }


  @media screen and (max-width: 355px) {
    
    .nav__menu{
        padding: 2rem .25rem 4rem;
       
    }
    .nav__list{
        display: grid;
        grid-template-columns: repeat(3,1fr);
        column-gap: 0;
    }

    
  }