.skills__container{
    grid-template-columns: repeat(3,1fr);
    column-gap: 3rem;
    justify-content: center;
    text-align: center;
}

.skills__content{
    background-color: var(--container-color);
    border: 1px solid rgba(0, 0, 0, .1);
    padding: 2rem 1rem;
    border-radius: 1.2rem;
}


.skills__title{
    font-size: var(--h2-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-1-5);
}

.skills__box{
    display: flex;
    justify-content: center;
    column-gap: 2.5rem;
}

.skills__group{
    display: grid;
    align-items: flex-start;
    row-gap: 1rem;
}

.skills__data{
    display: flex;
    column-gap: .5rem;
    text-align: left;
}

.bx-badge-check{
    font-size: 1.5rem;
    color: var(--title-color);
    margin-top: .2rem;
}



@media screen and (max-width: 768px) {
    
    .skills__container{
        grid-template-columns: repeat(1,1fr);
        grid-template-rows: repeat(3,1fr);
        column-gap: 3rem;
       
        justify-content: center;
        text-align: center;
        margin-inline: 5rem;
       
    }
    .skills__group{
        display: flex;
        gap: 1rem;
        flex-wrap: wrap;
        justify-content: center;
    }

    .skills__data{
        display: flex;
        column-gap: .5rem;
        text-align: left;

    }
    .skills__title{
        font-size: 1.5rem;
    }
    .skills__name{
        font-size: var(--h1-font-size);
    }
    
    .skills__level{
        font-size: var(--h2-font-size);
    }
}

@media screen and (max-width: 568px) {
    
    
    .skills{
        padding-inline: auto;
    }
  
    .skills__container{
        grid-template-columns: repeat(1,1fr);
        grid-template-rows: repeat(3,1fr);
        column-gap: 3rem;
       
        justify-content: center;
        text-align: center;
       margin-inline: 2rem;
    }
    

    .skills__group{
        display: flex;
        gap: 1rem;
        flex-wrap: wrap;
        justify-content: center;
    }

    .skills__data{
        display: flex;
        column-gap: .5rem;
        text-align: left;

    }
    .skills__title{
        font-size: 1.5rem;
    }
    .skills__name{
        font-size: var(--h1-font-size);
    }
    
    .skills__level{
        font-size: var(--h2-font-size);
    }
}

@media screen and (max-width: 376px){
    :root{
        --big-font-size: 1.75rem;
        --h1-font-size: 1rem;
        --h2-font-size: .8rem;
        --h3-font-size: .6rem;
        --normal-font-size: 0.5rem;
        --small-font-size: 0.4rem;
        --smaller-font-size: 0.3rem;
      }
   
    
}