*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

#contact-id{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    justify-content: center;
    align-items: center;
}
.contact__section p{
    font-size: 1rem;
}
#contact-id h1{
    font-size: 2rem;
}

.section__subMainTitle{
    font-size: 1.5rem;
}

.contact__section
{
    background-color: var(--container-color);
    margin-inline: auto;
   width: 650px;
    border: 1px solid rgba(0, 0, 0, 0.1); 
    padding: 2rem;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 1rem;
    margin-bottom: 2rem;
}



.contact__form{

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
}

.contact__name,
.contact__email,
.contact__desc{
    position: relative;
    display: flex;
    flex-direction: column;
    margin-bottom: 2.5rem;

}

.label{
    position: absolute;
    font-size: var(--h2-font-size);
    padding-left: .5rem;
    display: flex;
    background-color: var(--container-color);
    border: 0px;
    border-radius: 1rem;
    left: 0;
    top: -2.5rem;
    
    transition: all .4s;

}

.input-name,
.input-email,
.input-desc{
    width: 350px;
    padding: .3rem .5rem;
    border-radius: .6rem;
    border: 1px solid rgba(0, 0, 0, 0.5);
    
}

.input-name:focus ~ #name-label{
    
    font-size: .8rem;
    transform:  translateY(1.6rem) translateX(1rem);
    padding-right: .5rem;
    transition: all .4s;
    
}

.input-email:focus ~ #email-label{
    
    font-size: .8rem;
    transform:  translateY(1.6rem) translateX(1rem);
    padding-right: .5rem;
    transition: all .4s;
    
}

.input-desc:focus ~ #desc-label{
    
    font-size: .8rem;
    transform:  translateY(1.6rem) translateX(1rem);
    padding-right: .5rem;
    transition: all .4s;
    
}



.input-name,.input-email{
    height: 2.5rem;
    font-size: 1.1rem;
    margin-bottom: 1rem;
}

.input-desc{
    height: 9rem;
    font-size: 1.1rem;
    margin-bottom: 1rem;
}



.label-shift{

    font-size: .8rem;
    transform:  translateY(1.6rem) translateX(1rem);
    padding-right: .5rem;
    transition: all .4s;
    
}




@media screen and (max-width: 768px) {
    .label{
        position: absolute;
    
        font-size: var(--h1-font-size);
    padding-left: .5rem;
    padding-right: .5rem;
    display: flex;
    background-color: var(--container-color);
    border: 0px;
    border-radius: 1rem;
    left: 1rem;
    top: -.8rem;
    }

    
    .input-name,
    .input-email,
    .input-desc{
        font-size: .8rem;
        
    }
    
    .input-name:focus,.input-email:focus,.input-desc:focus{
        outline: none;
    }

    .input-name:focus ~ #name-label{
    
        transform: none;
        font-size: var(--h1-font-size);
    }
    
    .input-email:focus ~ #email-label{
        transform: none;
        font-size: var(--h1-font-size);
    }
    
    .input-desc:focus ~ #desc-label{
        transform: none;
        font-size: var(--h1-font-size);
    }


}


@media screen and (max-width: 568px) {
   
    .contact__section
    {
        background-color: var(--container-color);
        margin-inline: auto;
        width: 386px;
        border: 1px solid rgba(0, 0, 0, 0.1); 
        padding: 2rem;
        border-radius: 1rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        row-gap: 1rem;
        margin-bottom: 2rem;
    }

    
    
}

