.about__container{

    grid-template-columns: repeat(2,1fr);
    align-items: center;
    column-gap: 4rem;
}
.about__data{
    text-align: center;
}
.about__img{
    width: 400px;
    
    border-radius: 1.5rem;
    justify-content: center;

}
.section__title{
    font-size: var(--h1-font-size);
}
.section__subtitle{
    font-size: var(--h3-font-size);
}

.about__info{


    grid-template-columns: repeat(3,1fr);
    gap: 1rem;
    margin-bottom: var(--mb-2);
}

.about__box{

    background-color: var(--container-color);
    border: 1px solid rgba(0, 0, 0, .1);
    border-radius: 0.75rem;
    text-align: center;
    padding: 1rem 1.25rem;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.about__icon{

    font-size: 1.75rem;
    color: var(--title-color);
    margin-bottom: var(--mb-0-5);
}

.about__subtitle{
    font-size: var(--normal-font-size);
    font-weight: var(--font-medium);
}

.about__description{
    text-align: justify;
    padding: 0 2rem 0 2rem;
    margin-bottom: var(--mb-2-5);
    margin-inline: auto ;

}



@media screen and (max-width: 1080px) {
    .about__container{
      grid-template-columns: 1fr;
      row-gap: 2.5rem;
    }
    .about__img{
        width: 350px;
        margin-inline: auto;
    }

    .about__box{
        padding: 0.75rem 0.5rem;
    }

    .about__data{
        max-width: 80%;
        text-align: center;
        margin-inline: auto;
    }

    .about__info{
        justify-content: center;
    }


  }
  
  @media screen and (max-width: 768px) {
    :root{
        --big-font-size: 2rem;
        --h1-font-size: 1rem;
        --h2-font-size: .8rem;
        --h3-font-size: .6rem;
        --normal-font-size: 0.5rem;
        --small-font-size: 0.4rem;
        --smaller-font-size: 0.3rem;
      }

      .about__description{
        text-align: center;
        margin-bottom: var(--mb-2-5);
        padding-inline: 0px;

    }

    .about__info{

        
        gap: .5rem;
       
    }
    .about__img{
        width: 250px;
        margin-inline: auto;

    }
    .about__box{
        padding: 0.75rem 0rem;
    }
    .about__title{
        font-size: .7rem;
    }
    .about__subtitle{
        font-size: var(--h3-font-size);
    }
    .section__title{
        font-size: var(--big-font-size);

    }

    .section__subtitle{
        font-size: var(--h1-font-size);
    }

    .about__description{
        font-size: var(--h2-font-size);
    }
  }
  
  @media screen and (max-width: 355px) {
    :root{
        --big-font-size: 1.75rem;
        --h1-font-size: 1rem;
        --h2-font-size: .8rem;
        --h3-font-size: .6rem;
        --normal-font-size: 0.5rem;
        --small-font-size: 0.4rem;
        --smaller-font-size: 0.3rem;
      }
    .container{
      
      margin-inline: var(--mb-1);
    }

    .about__img{
        width: 150px;
        margin-inline: auto;

    }

    .section__title{
        font-size: var(--h2-font-size);

    }

    .section__subtitle,.about__description{
        font-size: var(--small-font-size);
    }
  }

  